html, body {
  height: 100%;
  margin: 0;
  display: flex;
  flex-direction: column;
  /* background-color: #121212; */
  background: linear-gradient(90deg, #121212 0%, #1C1C1C 100%);
  background-attachment: fixed; /* Evita la repetición al hacer scroll */
  background-repeat: no-repeat;
}

body{
  color: #b0b0b0;
  font-family: "Istok Web", serif;
  font-weight: 400;
  font-size: 1.2rem;
  font-style: normal;
}

p{
  text-align: justify;
}

.App{
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
}

.principal-container{
  flex: 1;
  max-width: 1200px;
  padding: 20px;
  margin: 5% 5% 0;
}

/* Textos */
.featured{
  text-align: center;
  font-size: 3rem;
	font-family: "Poppins", sans-serif;
  font-weight: bold;
  font-style: normal;
  color: #ffc107;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.6);
}

.featured-2{
  display: flex;
  font-family: "Montserrat", sans-serif;
  font-size: 2rem;
  color: #FFFFFF;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-optical-sizing: auto;
  font-weight: bold;
  font-style:normal;
  justify-content: center;
}

.istok-italic{
  font-style: italic;
}

.highlight{
  color: #FFC107;
}

.btn-a{
  color: #fff;
  font-weight: bold;
  text-decoration: none;
  padding: 10px 30px;
  border-radius: 5px 25px;
  background-color: #00a6ed;
  transition: background-color 0.3s ease, border-radius 0.3s ease, transform 0.3s ease, box-shadow 0.3s ease;
}

.btn-a:hover {
  transform: scale(1.1);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2), 0 0 15px rgba(0, 166, 237, 0.6);
  border-radius: 5px 5px;
  /* background-color: #007acc; */
  color: #000;
  background-color: #FFC107;
  box-shadow: 0 0 20px rgba(255, 193, 7, 0.7);
}

